@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
body,html{margin:0;padding:0;box-sizing:border-box}.App{background:#e0eafc;background:linear-gradient(to bottom, #CFDEF3, #E0EAFC);height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;font-family:"Heebo",sans-serif}.options{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100%}.sic{-webkit-animation:scale-in-center .5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;animation:scale-in-center .5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@-webkit-keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}
